@import url(https://fonts.googleapis.com/css?family=Orbitron:400,900);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, left top, right top, from(rgb(7, 134, 151)), to(rgb(19, 46, 80)));
  background: -webkit-linear-gradient(left, rgb(7, 134, 151) 0%, rgb(19, 46, 80) 100%);
  background: linear-gradient(to right, rgb(7, 134, 151) 0%, rgb(19, 46, 80) 100%)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    font-family: 'Orbitron', sans-serif;
    font-size: 5em!important;
    margin: .67em 0;
    color: #fff;
    text-shadow: 0px 20px 10px #0006;
}
